.accordion-button
{
	color: #004c7a;
}


.accordion-item
{

	border:none;
}


.accordion-button, .accordion-item 
{
	background-color: #ffe4c400;
}


.accordion-header
{

border:none;
}


.accordion-button:not(.collapsed) {
    color: #229fb1;
    font-weight: 600;
}


.accordion-button:not(.collapsed) {
    color: #004c7a;
    background-color: #ffe4c400;
    box-shadow: none;
}



.accordion-button:focus {
    z-index: 3;
    border-color: #86b7fe00;
    outline: 0;
    box-shadow: 0 0 0 0.25rem rgb(13 110 253 / 0%);
}




.active {
    background: #fff!important;
    border-radius: 13px!important;
    -webkit-box-shadow: 0 4px 10px 0 rgb(0 0 0 / 14%)!important;
    box-shadow: 0 4px 10px 0 rgb(0 0 0 / 14%)!important;
}



.accordion-body {
    padding: 1rem 1.25rem;
    padding-top: 10px;
}



.accordion-button.active
{
    color: #229fb1!important;
    font-weight: 600!important;
}



.left_profile_menu .second_nav p {
    margin-top: 0px;
}



.accordion-button::after {
    margin-left: 10px;
}


.accordion-button
{
    max-width: 170px;

}

